// https://html.spec.whatwg.org/multipage/canvas.html#the-canvas-element
import Node, { EXTEND_NODE } from '../node.js';
import { CanvasRenderingContext2D,
// ImageBitmaps currently unsupported
// https://developer.mozilla.org/en-US/docs/Web/API/ImageBitmap
//ImageBitmapRenderingContext, ImageBitmapRenderingContextSettings,
 } from './RenderingContext.js';
// Implementation
const WIDTH = Symbol("canvas-width");
const HEIGHT = Symbol("canvas-height");
const CONTEXT = Symbol("canvas-context");
const EID = Symbol("element-id");
